<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="用户ID" :required="true">
            <el-input v-model.number="requestParam.uid"></el-input>
          </el-form-item>
          <el-form-item label="金额范围">
            <el-input
              v-model.number="requestParam.diff_range[0]"
              placeholder="最低金额"
              size="small"
              class="minInput"
            ></el-input>
            -
            <el-input
              v-model.number="requestParam.diff_range[1]"
              placeholder="最高金额"
              size="small"
              class="minInput"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getList">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="showDataTab">
        <el-table
          :data="page.list"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column prop="timestamp" label="记录时间" align="center">
            <template slot-scope="scope">
              {{ scope.row.timestamp | formatDate }}
            </template>
          </el-table-column>
          <el-table-column prop="uid" label="用户ID" align="center">
          </el-table-column>
          <el-table-column prop="" label="充值项" align="center" >
              <span>-</span>
          </el-table-column>
          <el-table-column prop="" label="充值金额" align="center" >
               <span>-</span>
          </el-table-column>
          <el-table-column prop="diff" label="充值金币" align="center" >
          </el-table-column>
          <el-table-column prop="remark" label="订单号" align="center">
               <span>-</span>
          </el-table-column>
           <el-table-column prop="pre_totalGold" label="完成前总金币" align="center">
          </el-table-column>
          <el-table-column prop="post_totalGold" label="完成后总金币" align="center">
          </el-table-column>
        </el-table>
      </div>
      <div class="pageBar">
        <el-pagination
          background
          hide-on-single-page
          layout="prev, pager, next"
          @next-click="nextPage"
          @current-change="thisPage"
          @prev-click="prePage"
          :current-page="page.current_page"
          :page-size="page.page_size"
          :total="page.total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      requestParam: {
        uid: null,
        diff_range: [],
        changeType: 1
      },
      page: {
        list: [],
        current_page: 1,
        total: 0,
        page_size: 8
      }
    };
  },
  methods: {
    getList() {
      if (!this.requestParam.uid) {
        this.$message({
          showClose: true,
          message: "用户Id不能为空!",
          type: "error"
        });
        return;
      }
      if (
        this.requestParam.diff_range[0] != "undefined" &&
        this.requestParam.diff_range[1] != "undefined"
      ) {
        if (
          this.requestParam.diff_range[0] >= this.requestParam.diff_range[1]
        ) {
          this.$message({
            showClose: true,
            message: "金额上限必须小于下限!",
            type: "error"
          });
          return;
        }
      }
      var that = this;
      let query = {
        uid: this.requestParam.uid,
        diff_range:
          this.requestParam.diff_range == []
            ? null
            : this.requestParam.diff_range,
        changeType: this.requestParam.changeType,
        page: {
          current_page: this.page.current_page,
          page_size: this.page.page_size
        }
      };
      this.$api.statistic.list_goldChangeNoGame(query).then(res => {
        if (res.data.code === 200) {
          that.page.total = res.data.data.total;
          that.page.list = res.data.data.list;
          that.page.list.map((record, index) => {
            record.timestamp;
            that.page.list[index];
          });
        } else {
          this.$message({
            showClose: true,
            message: "获取列表失败",
            type: "error"
          });
        }
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 4 === 1) {
        return "warning-row";
      } else if (rowIndex % 4 === 3) {
        return "success-row";
      }
      return "";
    },
    prePage() {
      if (this.page.current_page > 0) {
        this.page.current_page--;
        this.getList();
      }
    },
    thisPage(cpage) {
      this.page.current_page = cpage;
      this.getList();
    },
    nextPage() {
      if (
        this.page.current_page <
        Math.ceil(this.page.total / this.page.page_size)
      ) {
        this.page.current_page++;
        this.getList();
      }
    },
    addColor(diff) {
      if (diff => 0) {
        return "greenCor";
      } else {
        return "redCor";
      }
    }
  },
  filters: {
    formatDate: function(value) {
      return moment(value).format("YYYY/MM/DD hh:mm:ss a");
    }
  }
};
</script>

<style lang="less" scoped>
.pageBar {
  width: 100%;
  height: 100px;
  // background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}
.innerForm {
  padding: 2% 5%;
  border: #eef1f6 1px solid;
  margin-left: -100px;
  //box-shadow: #eef1f6 0px 0px 10px;
}
.innerForm_item {
  margin: 10px 0;
}
.dialog_input_small {
  width: 35%;
  margin: 0 20px;
}
.minInput {
  width: 40%;
}
.greenCor {
  font-size: larger;
  color: green;
}
.redCor {
  color: red;
}
</style>



